@import url(https://fonts.googleapis.com/css?family=EB+Garamond);
h1,h2,h3,h4,h5,.btn{letter-spacing:1.2pt}*{font-family:Garamond, EB Garamond, Georgia, serif}p{letter-spacing:.6pt;line-height:16pt;max-width:80ch;font-family:Georgia, serif}h1{font-size:1.5rem;line-height:30pt}ul{margin-top:1rem}ul li{letter-spacing:0.6pt;line-height:16pt;margin-left:2rem}*{margin:0;padding:0;font-size:100%;line-height:1}.cardContainer{margin-top:2em;display:flex;flex-direction:row;flex-wrap:wrap;grid-gap:1em;gap:1em}.cardContainer .card{transition:box-shadow 500ms ease-out;transition:background-color 500ms ease-out}.cardContainer .card{border:solid #000;padding:1em;flex-grow:1;width:15em;max-width:30rem}.cardContainer .card:hover{box-shadow:1px 1px 4px dimgray,-0.3em 0.1em 0.4em dimgray;background-color:#d0d0d0}.cardContainer .card .projectName{text-align:center}.cardContainer .card .projectName{font-size:1.3rem;text-align:center}.cardContainer .card .hosted{background-color:#534292;color:white;display:flex;justify-content:center}.cardContainer .card .github{background-color:#24292e;color:white;display:flex;justify-content:center;align-content:space-around}.cardContainer .card .btn{padding:1em;margin:0.5em 0.1em;font-size:1.4rem;text-decoration:none;text-align:center}.cardContainer .card .btn:hover{box-shadow:1px 1px 1px dimgray,-0.1em 0.1em 0.1em dimgray;transition:all 0.2s ease-in-out}.cardContainer .card button{cursor:pointer}.cardContainer .card h2{color:#000}.cardContainer .card p{color:#000;margin:2rem 0}.searchTags{margin-top:4em;margin-left:20%;margin-right:20%;justify-items:center;height:2em;width:60%;font-size:1.5rem;transition:box-shadow 500ms ease-out;box-shadow:1px 1px 1px dimgray,-0.1em 0.1em 0.1em dimgray}.searchTags:focus{box-shadow:1px 1px 4px dimgray,-0.3em 0.1em 0.4em dimgray}.searchTags:hover{box-shadow:1px 1px 4px dimgray,-0.3em 0.1em 0.4em dimgray}.contact-form{flex-direction:column;display:flex;min-width:200px;max-width:600px;margin-left:auto;margin-right:auto}.contact-form textarea{resize:none;margin-top:1em;min-height:100px;font-size:1.4rem}.contact-form input{font-size:1.4rem;height:1.5rem;margin-top:1em}.contact-form button{width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;height:2rem;font-size:1rem;margin-left:auto;margin-top:.2rem;cursor:pointer}.skills{margin-top:1.5rem}.skills h3{font-size:1.2rem}#about-intro{width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;margin-left:auto;margin-right:auto}#about-intro p,#about-intro h2{padding-left:1rem}@media only screen and (min-width: 650px){#skills-div{box-sizing:border-box;width:100%;display:flex;justify-content:space-evenly;padding-left:clamp(0vw, 5vw, 10vw);grid-gap:5rem;gap:5rem}#skills-div h3{text-align:center}#skills-div section ul{max-width:45ch}}body{margin:0;background:linear-gradient(#d0d0d0, dimgray);background-repeat:round;position:relative;padding-bottom:11rem}.container{min-height:100vh;width:80%;margin:auto}header{height:8rem;text-align:center;padding:1rem;color:#000;background-color:transparent}nav{background-color:transparent;font-size:3rem}nav .burger{text-decoration:none}nav ul{list-style-type:none;margin:0}nav ul .btn{font-size:1.5rem;padding:.7rem 0;cursor:pointer;width:100%;background-color:#efefef}nav ul .btn:hover{color:#d0d0d0;background-color:dimgray}.underlined{text-decoration:underline}@media only screen and (min-width: 500px){nav .btn{padding:1rem;max-width:8em;font-size:1.5rem;min-width:4em}nav ul{display:flex;justify-content:center;grid-gap:2rem;gap:2rem}}.hidden{font-size:5rem}footer{position:absolute;border-top:0.1rem solid #000;box-sizing:border-box;bottom:0;background-color:dimgray;left:0;width:100%}footer .scocials{height:10em;display:flex;align-items:center;justify-content:center}footer .scocials ul{margin:0;list-style:none;display:flex;grid-gap:1em;gap:1em}

